'use strict';

import { WinJSDec, fnError } from './APP/config';

var name = 'YX_JUMP_YYJK';
var YX_JUMP_YYJK = (url) => {
  if (url) {
    WinJSDec(_ => {
      WinJSBridge.call('tools', 'wakeupapp', { url: url });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default YX_JUMP_YYJK;